.gallery {
  background: url(../images/background-desktop@2x.png) rgb(252, 252, 252) 50% / cover no-repeat;
  width: 100%;
  height: 500px;
}

.gallery-list {
  position: relative;
  overflow: hidden;
  max-width: 1013px;
  margin: 0 auto;
}
.gallery-item {
  animation-name: gallery;
  animation-duration: 9s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}
.gallery-item:nth-child(n + 2) {
  position: absolute;
  top: 0;
}
.gallery-item:nth-of-type(1) {
  animation-delay: 6s;
}
.gallery-item:nth-of-type(2) {
  animation-delay: 3s;
}
.gallery-item img {
  border-radius: 10px;
}

@keyframes gallery {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  33% {
    opacity: 1;
  }
  43% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
